.gallery {
  background: linear-gradient(to bottom, #fff5f6 45%, #ffffff 45%, #ffffff 100%);
}
.grid {
  display: grid;
  justify-content: center;
  align-items: center;

  @media screen and(max-width:767px) {
    height: 122px;
    grid-column-gap: 3px;
    grid-row-gap: 3px;
  }
  @media screen and (min-width: 768px) {
    height: 264px;
    grid-column-gap: 6px;
    grid-row-gap: 6px;
  }
  @media screen and (min-width: 1280px) {
    height: 430px;
  }
}
.grid:hover {
  opacity: 1;
  .grid__item & {
    opacity: 0;
  }
}

.grid:hover img {
  opacity: 0.3;
  z-index: 1;
}
.grid__item img {
  transition: opacity 1000ms $transition;
}
.grid__item img:hover {
  opacity: 1;
}
.grid__item {
  transition: transform 250ms $transition, box-shadow 250ms $transition;
  overflow: hidden;
  border-radius: 2px;

  @media screen and(min-width: 768px) {
    border-radius: 10px;
  }
  @media screen and(min-width: 1280px) {
    border-radius: 22px;
  }
  &:hover {
    transform: scale(1.25);
    box-shadow: 0px 0px 5px 1px rgba(212, 20, 67, 0.2);
    z-index: 1000;
  }
  &:nth-child(1) {
    grid-area: 4 / 1 / 6 / 2;
  }
  &:nth-child(2) {
    grid-area: 3 / 2 / 5 / 3;
  }
  &:nth-child(3) {
    grid-area: 5 / 2 / 7 / 3;
  }
  &:nth-child(4) {
    grid-area: 6 / 3 / 8 / 4;
  }
  &:nth-child(5) {
    grid-area: 5 / 5 / 7 / 6;
  }
  &:nth-child(6) {
    grid-area: 3 / 5 / 5 / 6;
  }
  &:nth-child(7) {
    grid-area: 2 / 6 / 4 / 7;
  }
  &:nth-child(8) {
    grid-area: 4 / 6 / 6 / 7;
  }
  &:nth-child(9) {
    grid-area: 3 / 7 / 7 / 8;
  }
  &:nth-child(10) {
    grid-area: 1 / 3 / 6 / 5;
  }
}

