.products__title {
  margin-bottom: 144px;

  font-family: 'Titan One', sans-serif;
  @include font($fs: normal, $fz: 34px, $fw: 400, $lh: 39px, $ls: 0.04em);
  text-align: center;
  text-transform: uppercase;
  color: $primary-accent-color;

  @media screen and (min-width: 768px) {
    margin-bottom: 150px;

    @include font($fs: normal, $fz: 48px, $fw: 400, $lh: 55px, $ls: 0.04em);
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 259px;

    @include font($fs: normal, $fz: 58px, $fw: 400, $lh: 66px, $ls: 0.04em);
  }
}

.products__pretitle {
  margin-bottom: 15px;

  font-family: 'Titan One', sans-serif;
  @include font($fs: normal, $fz: 18px, $fw: 400, $lh: 21px, $ls: 0.04em);
  text-align: center;
  text-transform: uppercase;
  color: $secondary-accent-color;

  @media screen and (min-width: 768px) {
    @include font($fs: normal, $fz: 26px, $fw: 400, $lh: 30px, $ls: 0.04em);
  }

  @media screen and (min-width: 1280px) {
    @include font($fs: normal, $fz: 30px, $fw: 400, $lh: 34px, $ls: 0.04em);
  }
}

.products__list {
  @media screen and (min-width: 768px) {
    display: flex;
    margin: 0 -10px;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    margin: 0 -15px;
  }
}
