.products__caption {
  position: relative;

  font-family: 'Titan One', sans-serif;
  @include font($fs: normal, $fz: 20px, $fw: 400, $lh: 23px, $ls: 0.04em);
  text-align: center;
  text-transform: uppercase;

  margin-bottom: 66px;
  transition-property: color, text-shadow;
  transition-duration: 1000ms;
  transition-delay: 1300ms;
  transition-timing-function: linear;
  @media (min-width: 768px) {
    @include font(normal, 20px, 400, 23px, 0.04em);
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 70px;

    @include font($fs: normal, $fz: 30px, $fw: 400, $lh: 34px, $ls: 0.04em);
  }
}

.products__caption::after {
  content: '';

  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%);

  display: inline-block;

  width: 26px;
  height: 6px;

  background-image: url('../images/desktop/group.png');
  background-repeat: no-repeat;
  background-size: contain;

  margin-top: 32px;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/desktop/group@2x.png');
  }
}

.products__caption--icecream::before {
  content: '';

  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translate(-50%);

  display: inline-block;

  width: 249px;
  height: 252px;

  margin-bottom: 22px;

  background-image: url('../images/mobile/product1_mob.png');
  background-size: contain;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/mobile/product1_2x_mob.png');
