.products__description {
  @include font($fs: normal, $fz: 14px, $fw: 700, $lh: 21px, $ls: 0.04em);
  text-align: center;
  text-transform: uppercase;

  margin-bottom: 30px;

  @media screen and (min-width: 768px) {
    @include font(normal, 14px, 700, 25px, 0.04em);
    margin-bottom: 40px;
    text-transform: none;
  }

  @media screen and (min-width: 1280px) {
    @include font(normal, 16px, 700, 28px, 0.04em);
    margin-bottom: 62px;
  }
  transition-property: opacity;
  transition-duration: 1000ms;
  transition-delay: 1300ms;
  transition-timing-function: linear;
}

.products__button {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-size: contain;
  transition-property: rotateY();
  transition-duration: 1000ms;
  transition-delay: 1300ms;
  transition-timing-function: linear;

  background-image: url('../images/desktop/arrow-right.png');
  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/desktop/arrow-right@2x.png');
  }

  margin-left: auto;
  margin-right: auto;
  // -----------------------
  position: absolute;
  left: 43%;
  bottom: 40px;
  @media (min-width: 768px) {
    left: 40%;
  }
  @media (min-width: 1280px) {
    left: 45%;
  }
}

.products__button:hover + .products__item {
  transform: rotateY(180deg);
}
.products__description--hidden {
  @media (min-width: 768px) {
    display: none;
  }
}
// ------------стилі для анімації----------

.products__scene {
  // --------скопійовані стилі з .products__item ----------
  position: relative;
  border-radius: 24px;
  padding: 161px 40px 40px 40px;
  margin-bottom: 130px;
  height: 525px;
  @media screen and (min-width: 768px) {
    padding: 152px 11px 40px 11px;
    margin: 0 10px;
    height: 485px;
  }

  @media screen and (min-width: 1280px) {
    padding: 163px 44px 80px 50px;
    margin: 0 15px;
    max-width: 335px;
  }
  // ------------------------------------------
  transition-duration: 2s;
  transition-timing-function: linear;
  transform: perspective(150px);
  background-color: orangered;
}

.products__scene--icecream {
  background-color: $secondary-accent-color;
}

.products__scene--icecoffee {
  background-color: #f0d1a5;
}

.products__scene--milkshakes {
  background-color: #c2e297;
}
.products__scene.is-open {
  transform: perspective(500px) rotateY(360deg);
  background-color: rgb(247, 250, 82);
}
.products__scene.is-open .products__description {
  opacity: 0;
}
.products__read-more {
  position: absolute;
  opacity: 0;
  left: 0;
  top: 230px;
  text-align: center;
  padding: 25px;
  @include font(normal, 12px, 700, 19px, normal);
  color: $primary-accent-color;

  transition-property: opacity;
  transition-duration: 1000ms;
  transition-delay: 1300ms;
  transition-timing-function: linear;
  @media (min-width: 768px) {
    top: 195px;
    @include font(normal, 10px, 700, 19px, normal);
  }
  @media (min-width: 1280px) {
    top: 220px;
    @include font(normal, 14px, 700, 19px, normal);
  }
}
.products__scene.is-open .products__read-more {
  opacity: 1;
}
.products__scene.is-open .products__caption {
  color: $primary-accent-color;
  text-shadow: 2px 2px 4px #000000;
}
.products__scene.is-open .products__button {
  transform: rotateY(180deg);
}
